

import Overlay from './Overlay';
import Button from './Button';
import 'veui-theme-x/components/Button.js';
import 'veui-theme-x/components/button.less';
import { ui, icons, overlay } from '../mixins';
import { drag } from '../directives';
import Icon from './Icon';

import 'veui-theme-x/components/icon.less';
export default {
  name: 'veui-dialog',
  components: {
    'veui-overlay': Overlay,
    'veui-button': Button,
    'veui-icon': Icon
  },
  directives: { drag: drag },
  mixins: [ui, icons, overlay],
  props: {
    ui: String,
    modal: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      default: null
    },
    open: {
      type: Boolean,
      default: false
    },
    closable: {
      type: Boolean,
      default: true
    },
    draggable: {
      type: Boolean,
      default: false
    },
    priority: Number
  },
  data: function data() {
    return {
      localOpen: this.open,
      localWidth: this.width,
      localClosable: this.closable,
      localHeight: this.height,
      localTitle: this.title || null
    };
  },

  watch: {
    title: function title(value) {
      this.localTitle = value;
    },
    open: function open(value) {
      this.localOpen = value;
    },
    width: function width(value) {
      this.localWidth = value;
    },
    height: function height(value) {
      this.localHeight = value;
    },
    closable: function closable(value) {
      this.localClosable = value;
    }
  },
  methods: {
    hide: function hide() {
      this.localOpen = false;
      this.$emit('update:open', this.localOpen);
    },
    focus: function focus() {
      if (this.$refs.overlay) {
        this.$refs.overlay.focus();
      }
    },
    dragReady: function dragReady(handle) {
      this.dragHandle = handle;
    },
    resetPosition: function resetPosition() {
      if (!this.dragHandle) {
        throw new Error('The dialog is not ready for drag');
      }

      this.dragHandle.reset();
    }
  }
};


