

import Icon from './Icon';
import 'veui-theme-x/components/icon.less';
import { icons } from '../mixins';

export default {
  name: 'toast',
  mixins: [icons],
  components: {
    'veui-icon': Icon
  },
  props: {
    ui: String,
    type: {
      type: String,
      default: 'success'
    },
    message: String,
    duration: {
      type: Number,
      default: 3000
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.timer = setTimeout(function () {
      _this.$emit('close');
    }, this.duration);

    this.$emit('ready', this.$el.offsetHeight);
  },
  beforeDestroy: function beforeDestroy() {
    clearTimeout(this.timer);
  }
};


