

import { Uploader } from 'veui';
import 'veui-theme-x/components/Uploader.js';
import 'veui-theme-x/components/uploader.less';
import { ui } from 'veui/mixins';

export default {
  name: 'uploader-demo',
  components: {
    'veui-uploader': Uploader
  },
  data: function data() {
    return {
      files: [{
        name: 'demo-file1.jpg',
        fileUid: '123456',
        size: '200kb',
        src: 'https://www.baidu.com/img/bd_logo1.png'
      }, {
        name: 'demo-file2.gif',
        fileUid: '222333',
        size: '350kb',
        src: 'http://nodejs.cn/static/images/logo.svg'
      }],
      filesIframe: 'http://nodejs.cn/static/images/logo.svg',
      payload: {
        year: '2017',
        month: '4'
      }
    };
  },
  computed: {
    uiPropsIframe: function uiPropsIframe() {
      if (!this.uiIframe || !this.uiIframe.trim()) {
        return [];
      }
      return this.uiIframe.trim().split(/\s+/);
    }
  },
  mixins: [ui],
  methods: {
    onSuccess: function onSuccess(data) {
      console.log(data);
    },
    onFailure: function onFailure(data) {
      console.log(data);
    },
    mockResult: function mockResult() {
      var _this = this;

      setTimeout(function () {
        if (_this.filesIframe) {
          _this.$refs.iframeUploader.uploadCallback({
            status: 'success',
            name: 'demo-file.jpg'
          }, _this.$refs.iframeUploader.currentSubmitingFile);
        }
      }, 1000);
    }
  }
};


