import _uniqueId from 'lodash/uniqueId';
import _findIndex from 'lodash/findIndex';
import _assign from 'lodash/assign';


import Overlay from './Overlay';
import Toast from './Toast';
import 'veui-theme-x/components/Toast.js';
import 'veui-theme-x/components/toast.less';

import config from '../managers/config';
import { overlay } from '../mixins';

config.defaults({
  'toast.priority': 101
});

export default {
  name: 'toast-list',
  mixins: [overlay],
  components: {
    'veui-overlay': Overlay,
    'veui-toast': Toast
  },
  data: function data() {
    return {
      messages: [],
      priority: config.get('toast.priority')
    };
  },

  methods: {
    add: function add(message) {
      this.messages.unshift(_assign({ height: 0, top: 0 }, message, {
        __message_id__: _uniqueId('veui-toast-')
      }));
    },
    remove: function remove(message) {
      var index = _findIndex(this.messages, function (msg) {
        return msg.__message_id__ === message.__message_id__;
      });
      this.messages.splice(index, 1);
      this.updateLayout();
    },
    updateHeight: function updateHeight(message, index, height) {
      message.height = height;
      this.updateLayout();
    },
    updateLayout: function updateLayout() {
      var _this = this;

      this.messages.forEach(function (msg, i) {
        if (i === 0) {
          msg.top = 0;
          return;
        }

        var prev = _this.messages[i - 1];
        msg.top = prev.top + prev.height + 10;
      });
    }
  }
};


